.roadmap {
  display: grid;
  grid-template-columns: 228px 1fr;
  grid-auto-rows: 72px;
}

@media screen and (max-width: 1024px) {
  .roadmap {
    grid-template-columns: 140px 1fr;
  }
}

@media screen and (max-width: 600px) {
  .roadmap {
    grid-template-columns: 100px 1fr;
    grid-auto-rows: 70px;
  }
}